* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.countri-item {
  list-style: none;
}
.flag {
  width: 20px;
  /* height: 100px; */
}
.countri-item {
  display: flex;
  gap: 20px;

  height: 100%;
}
.capital,
.population,
.languages {
  font-weight: 600;
}
